import supabase from '../supabase'; // Cliente do Supabase

const deleteCorte = async (id) => {
    // Tenta excluir o corte
    const { data, error, count } = await supabase
        .from('cortes')
        .delete()
        .eq('id', id)
        .select(); // Adicionando select para forar o retorno dos dados excludos

    // Verificar se houve erro
    if (error) {
        console.error('Erro ao excluir corte:', error.message);
        return null; // Retorna null em caso de erro
    }

    console.log('Dados excludos:', data); // Log para ver se os dados foram excludos

    // Verificar se algum corte foi excludo
    if (data && data.length > 0) {
        console.log('Corte excludo com sucesso');
    } else {
        console.log('Nenhum corte encontrado para excluir');
    }

    // Retorna uma confirmao de que o corte foi excludo
    return data && data.length > 0;
};

// Exportando a funo
export { deleteCorte };
